	      // ...
	    ],
	  },
	  watch: {
	    locale(val){
	      this.$i18n.locale = val
	      // this.$store.dispatch('changeLocale', {
	      //   locale: val
	      // })
	    },
	    getLocale(val){
	    	this.locale = val
	    }
	  },
    computed: {
      getLocale(){
        return this.$i18n.locale
      }
    },
	  mounted(){
	    this.locale = this.getLocale
	  },
	  filters: {
	    spacer: function (value) {
	      if (!value) return ''
	      value = value.toString().split(' ').join('\xa0')
	      return value.split('').join('\xa0\xa0\xa0')
	    }
	  },
	}
